import {AfterContentInit, AfterViewInit, Component, HostListener, NgZone, OnInit} from '@angular/core';
import {delay} from 'rxjs/operators';
import {Timestamp} from 'rxjs';
import DateTimeFormat = Intl.DateTimeFormat;
import {formatDate} from '@angular/common';
import {MAT_DATE_LOCALE} from '@angular/material';

@Component({
  selector: 'app-watch',
  templateUrl: './watch.component.html',
  styleUrls: ['./watch.component.css']
})
export class WatchComponent implements AfterContentInit {

  private startTime: Date = null;
  private currentTime: string = null;
  private times : number[] = [];
  private interval

  isLandscape: boolean = true;

  constructor() {

  }

  @HostListener('window:orientationchange')
  private checkOrientation() {
    if (window.innerHeight > window.innerWidth) {
      this.isLandscape = false;
    } else {
      this.isLandscape = true;
    }
  }

  ngAfterContentInit(): void {
    this.checkOrientation();
  }

  startTimer(){
    this.startTime = new Date()
    this.interval = setInterval(()=> {this.updateTime()},50)
  }

  updateTime(){
    const currentTime = new Date().getTime()
    const difference = currentTime - this.startTime.getTime()
    this.currentTime = formatDate(difference,"mm:ss.SS","en-EN")
  }

  stopTime() {

    this.times.push(new Date().getTime() - this.startTime.getTime())

  }

  resetTime(){
    clearInterval(this.interval)
    this.currentTime = null
    this.startTime = null
    this.times = []
  }
}
